
[data-v-2df2e9d2]:root {
  --header-height: 60px;
  --leftbar-width: 20%;
  --rightbar-width: 14%;
  --content-lr-padding: 40px;

  /** colors */
  --primary-color: #fb6176;
  --border-color: #f5f3f7;
  --header-bg: #20232a;
  --header-color: white;
  --header-border-color: #20232a;
  --nav-border-color: #fb6176;
  --nav-border-color: var(--primary-color);
  --banner-bg: #282c34;
  --banner-color: white;
  --black: #20232a;
  --leftbar-bg: rgb(247, 247, 247);
}
.docs[data-v-2df2e9d2] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex
}
.docs.has-leftbar .content[data-v-2df2e9d2] {
  padding-left: 20%;
  padding-left: var(--leftbar-width);
}
.docs.has-rightbar:not(.has-leftbar) .main[data-v-2df2e9d2] {
  max-width: 90%;
  margin: 0 auto;
}
.main[data-v-2df2e9d2] {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}
.content[data-v-2df2e9d2] {
  padding-top: 60px;
  padding-top: var(--header-height);
  width: 86%;
  width: calc(100% - var(--rightbar-width));
}
.markdown-body[data-v-2df2e9d2] {
  padding: 20px 40px;
  padding: 20px var(--content-lr-padding);
}
.doc-right[data-v-2df2e9d2] {
  width: 14%;
  width: var(--rightbar-width);
  top: 60px;
  top: var(--header-height);
  padding: 30px 0;
  position: fixed;
  overflow: auto;
  bottom: 0;
  right: 0;
  z-index: 10;
}
