
[data-v-ccd280a6]:root {
  --header-height: 60px;
  --leftbar-width: 20%;
  --rightbar-width: 14%;
  --content-lr-padding: 40px;

  /** colors */
  --primary-color: #fb6176;
  --border-color: #f5f3f7;
  --header-bg: #20232a;
  --header-color: white;
  --header-border-color: #20232a;
  --nav-border-color: #fb6176;
  --nav-border-color: var(--primary-color);
  --banner-bg: #282c34;
  --banner-color: white;
  --black: #20232a;
  --leftbar-bg: rgb(247, 247, 247);
}
.leftbar[data-v-ccd280a6] {
  width: 20%;
  width: var(--leftbar-width);
  border-right: 1px solid rgb(236, 236, 236);
  position: fixed;
  top: 60px;
  top: var(--header-height);
  bottom: 0;
  left: 0;
  padding: 30px 0;
  background-color: rgb(247, 247, 247);
  background-color: var(--leftbar-bg);
  overflow: auto;
  z-index: 10
}
.leftbar[data-v-ccd280a6] .toc-item {
  font-size: inherit;
  padding: 3px 20px;
  border-left: 3px solid transparent;
  padding-left: 17px;
}
.leftbar[data-v-ccd280a6] .toc-item[data-depth="3"] {
  padding-left: 37px;
}
.leftbar[data-v-ccd280a6] .toc-item.router-link-exact-active {
  border-left-color: #fb6176;
  border-left-color: var(--primary-color);
}
