/**
 * prism.js tomorrow night eighties for JavaScript, CoffeeScript, CSS and HTML
 * Based on https://github.com/chriskempson/tomorrow-theme
 * @author Rose Pritchard
 */
.token.comment,
 .token.block-comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
   color: #999;
}
.token.punctuation {
   color: #ccc;
}
.token.tag,
 .token.attr-name,
 .token.namespace,
 .token.deleted {
   color: #e2777a;
}
.token.function-name {
   color: #6196cc;
}
.token.boolean,
 .token.number,
 .token.function {
   color: #f08d49;
}
.token.property,
 .token.class-name,
 .token.constant,
 .token.symbol {
   color: #f8c555;
}
.token.selector,
 .token.important,
 .token.atrule,
 .token.keyword,
 .token.builtin {
   color: #cc99cd;
}
.token.string,
 .token.char,
 .token.attr-value,
 .token.regex,
 .token.variable {
   color: #7ec699;
}
.token.operator,
 .token.entity,
 .token.url {
   color: #67cdcc;
}
.token.important,
 .token.bold {
   font-weight: bold;
}
.token.italic {
   font-style: italic;
}
.token.entity {
   cursor: help;
}
.token.inserted {
   color: green;
}
 /* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}
#nprogress .bar {
  background: #0099e5;

  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;

  width: 100%;
  height: 3px;
}
 /* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  opacity: 1;
  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}
.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}
:root {
  --header-height: 60px;
  --leftbar-width: 20%;
  --rightbar-width: 14%;
  --content-lr-padding: 40px;

  /** colors */
  --primary-color: #fb6176;
  --border-color: #f5f3f7;
  --header-bg: #20232a;
  --header-color: white;
  --header-border-color: #20232a;
  --nav-border-color: #fb6176;
  --nav-border-color: var(--primary-color);
  --banner-bg: #282c34;
  --banner-color: white;
  --black: #20232a;
  --leftbar-bg: rgb(247, 247, 247);
}
.pretty-scrollbar::-webkit-scrollbar {
   width: 6px;
   height: 6px;
}
.pretty-scrollbar::-webkit-scrollbar-thumb {
   background: #fb6176;
   background: var(--primary-color);
}
.pretty-scrollbar::-webkit-scrollbar-track {
   background: #f5f3f7;
}
.container {
  max-width: 90%;
  margin: 0 auto;
  padding: 0 20px;
}
.button {
  color: black;
  border: none;
  background-color: #fb6176;
  background-color: var(--primary-color);
  padding: 15px 30px;
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  cursor: pointer;
  text-decoration: none;
  -webkit-transition: all .3s ease;
  transition: all .3s ease
}
.button:hover {
   background-color: white;
}
.button .icon {
   margin-left: 5px;
   width: 20px;
   height: 20px;
}
pre, pre[class*="language-"] {
  border-radius: 6px;
  padding: 25.2px;
  padding: 1.575rem;
  white-space: pre-wrap;
  word-break: break-word;
  margin: 30px 0;
  color: white;
  background-color: #20232a;
  background-color: var(--black);
  overflow: auto
}
pre .docute-highlight-line, pre[class*="language-"] .docute-highlight-line {
   background-color: #14161a;
   display: block;
   margin: 0 -25.2px;
   margin: 0 -1.575rem;
   padding: 0 25.2px;
   padding: 0 1.575rem;
}
code {
  font-size: 16px;
  padding: 3px 5px;
  background: rgba(255, 229, 100, .2);
  color: #1a1a1a;
}
pre > code {
  padding: 0;
  font-size: 16px;
  background: transparent;
  color: white;
}
.markdown-body {
  font-size: 17px
}
.markdown-body a {
   text-decoration: none;
   color: #fb6176;
   color: var(--primary-color);
}
.markdown-body a:hover {
   text-decoration: underline;
}
.markdown-body h1, .markdown-body h2, .markdown-body h3, .markdown-body h4, .markdown-body h5, .markdown-body h6 {
   font-weight: bold;
   color: #20232a;
   color: var(--black);
}
.markdown-body h1:first-child, .markdown-body h2:first-child, .markdown-body h3:first-child, .markdown-body h4:first-child, .markdown-body h5:first-child, .markdown-body h6:first-child {
   margin-top: 0;
}
.markdown-body h1 a, .markdown-body h2 a, .markdown-body h3 a, .markdown-body h4 a, .markdown-body h5 a, .markdown-body h6 a {
   color: #20232a;
   color: var(--black);
}
.markdown-body h1 {
   font-size: 38.4px;
   font-size: 2.4rem;
}
.markdown-body h2 {
   font-size: 33.6px;
   font-size: 2.1rem;
   margin-top: 70px;
   margin-bottom: 30px;
   padding-bottom: 10px;
   border-bottom: 1px solid #f5f3f7;
   border-bottom: 1px solid var(--border-color);
}
.markdown-body h3 {
   font-size: 24px;
   font-size: 1.5rem;
   margin-top: 50px;
   margin-bottom: 20px;
   position: relative;
}
.markdown-body h3:before {
   content: '#';
   position: absolute;
   left: 0;
   -webkit-transform: translateX(-150%);
           transform: translateX(-150%);
   color: #fb6176;
   color: var(--primary-color);
   font-weight: bold;
}
.markdown-body h4 {
   font-size: 20.8px;
   font-size: 1.3rem;
}
.markdown-body h5 {
   font-size: 16px;
   font-size: 1rem;
}
.markdown-body img {
   max-width: 100%;
}
.markdown-body hr {
   border: none;
   height: 1px;
   background-color: #f5f3f7;
   margin: 30px 0;
}
.markdown-body ul li, .markdown-body ol li {
   margin-bottom: 5px;
}
.markdown-body p {
   margin: 0 0 30px 0;
   line-height: 1.7;
}
.markdown-body blockquote {
   background-color: rgba(255, 229, 100, .3);
   border-left-color: #ffe564;
   border-left-width: 9px;
   border-left-style: solid;
   padding: 20px;
   margin: 30px 0;
}
.markdown-body blockquote p:first-of-type {
   font-weight: 700;
}
.markdown-body blockquote >*:last-child {
   margin-bottom: 0;
}
.markdown-body blockquote p {
   margin: 0;
}
.markdown-body blockquote p:not(:first-child) {
   margin-top: 15px;
}
.markdown-body .alert p {
   margin: 0;
   padding-left: 30px;
   position: relative;
}
.markdown-body .alert p:not(:first-child) {
   margin-top: 15px;
}
.markdown-body .alert {
   background: #f9f9f9;
   border: 1px solid #efefef;
   border-radius: 5px;
   color: #373e42;
   padding: 15px 20px;
   margin: 25px 0;
}
.markdown-body .alert .alert-icon {
   margin-top: 2px;
   position: absolute;
   left: -5px;
   color: #949494;
}
.markdown-body .alert.danger {
   background: rgba(224, 76, 76, .08);
   border-color: rgba(224, 76, 76, .3);
}
.markdown-body .alert.danger .alert-icon {
   color: rgba(224, 76, 76, .92);
}
.markdown-body .alert.info {
   background: #fafdff;
   border-color: #d9eaf3;
}
.markdown-body .alert.info .alert-icon {
   color: rgb(116, 196, 239);
}
.markdown-body .alert.warning {
   background: rgba(253, 200, 69, .1);
   border-color: rgba(253, 200, 69, .3);
}
.markdown-body .alert.warning .alert-icon {
   color: rgb(245, 190, 88);
}
.markdown-body .alert.success {
   background: #f6fff5;
   border-color: rgba(58, 193, 51, .12);
}
.markdown-body .alert.success .alert-icon {
   color: rgba(34, 191, 26, .78);
}
body {
  font: 16px/1.4 -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
